html {
	font-family: sans-serif;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
img {
	max-width: 100%;
	height: auto;
}
body {
	margin: 0;
    @include bp($phone--max){
        background-color: $color-1-dark;
    }
}
*, *:before, *:after {
    box-sizing: border-box;
}
